/*--------------------------------------------------------------
# Normalize
--------------------------------------------------------------*/
* {
	position: relative;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

html {
	font-family: sans-serif;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%;
}

body {
	margin: 0;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
	display: block;
}

audio,
canvas,
progress,
video {
	display: inline-block;
	vertical-align: baseline;
}

audio:not([controls]) {
	display: none;
	height: 0;
}

[hidden],
template {
	display: none;
}

a {
	background-color: transparent;
}

a:active,
a:hover {
	outline: 0;
}

abbr[title] {
	border-bottom: 1px dotted;
}

b,
strong {
	font-weight: bold;
}

dfn {
	font-style: italic;
}

h1 {
	font-size: 2em;
	margin: 0.67em 0;
}

mark {
	background: #ff0;
	color: #000;
}

small {
	font-size: 80%;
}

sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline;
}

sup {
	top: -0.5em;
}

sub {
	bottom: -0.25em;
}

img {
	border: 0;
	display: block;
}

svg:not(:root) {
	overflow: hidden;
}

figure {
	margin: 1em 40px;
}

hr {
	box-sizing: content-box;
	height: 0;
}

pre {
	overflow: auto;
}

code,
kbd,
pre,
samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

button,
input,
optgroup,
select,
textarea {
	color: inherit;
	font: inherit;
	margin: 0;
}

button {
	overflow: visible;
}

button,
select {
	text-transform: none;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
	-webkit-appearance: button;
	cursor: pointer;
}

button[disabled],
html input[disabled] {
	cursor: default;
}

button::-moz-focus-inner,
input::-moz-focus-inner {
	border: 0;
	padding: 0;
}

input {
	line-height: normal;
}

input[type="checkbox"],
input[type="radio"] {
	box-sizing: border-box;
	padding: 0;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
	height: auto;
}

input[type="search"] {
	-webkit-appearance: textfield;
	box-sizing: content-box;
}

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none;
}

fieldset {
	border: 1px solid #c0c0c0;
	margin: 0 2px;
	padding: 0.35em 0.625em 0.75em;
}

legend {
	border: 0;
	padding: 0;
}

textarea {
	overflow: auto;
}

optgroup {
	font-weight: bold;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

td,
th {
	padding: 0;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	color: #21759b;
	display: block;
	font-size: 17.5px;
	font-size: 1.09375rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption.aligncenter,
.centered-image {
	max-width: 100vw;
	margin: 0 -1em 1.6em;
}

@media screen and (min-width: 37.5rem) {
	.wp-caption.aligncenter,
	.centered-image {
		margin: 0 -2em 1.6em;
	}
}

blockquote.alignleft,
blockquote.alignright,
blockquote.aligncenter {
	width: 50%;
	padding: 1em;
	margin: 0 1.5em 1.5em 0;
	background: none;
}

blockquote.alignleft:before, blockquote.alignleft:after,
blockquote.alignright:before,
blockquote.alignright:after,
blockquote.aligncenter:before,
blockquote.aligncenter:after {
	border: none;
}

blockquote.alignleft p,
blockquote.alignright p,
blockquote.aligncenter p {
	margin-bottom: 0;
}

blockquote.alignleft em, blockquote.alignleft i,
blockquote.alignright em,
blockquote.alignright i,
blockquote.aligncenter em,
blockquote.aligncenter i {
	font-style: italic;
	font-weight: 400;
}

blockquote.alignleft strong,
blockquote.alignright strong,
blockquote.aligncenter strong {
	color: inherit;
}

blockquote.alignleft:after {
	border-right: 2px solid #51CF66;
	border-top: 2px solid #51CF66;
	padding-bottom: 0;
	position: absolute;
	top: 0;
	right: 0;
	height: 50%;
	width: 100%;
}

blockquote.alignright:after {
	border-left: 2px solid #51CF66;
	border-bottom: 2px solid #51CF66;
	padding-top: 0;
	position: absolute;
	bottom: 0;
	left: 0;
	height: 50%;
	width: 100%;
}

blockquote.aligncenter {
	width: 100%;
	padding: 1.5em;
}

blockquote.aligncenter:before {
	border-right: 2px solid #51CF66;
	border-top: 2px solid #51CF66;
	padding-bottom: 0;
	position: absolute;
	top: 1.25em;
	right: 0;
	height: 50%;
	width: 50%;
}

blockquote.aligncenter:after {
	border-left: 2px solid #51CF66;
	border-bottom: 2px solid #51CF66;
	padding-top: 0;
	position: absolute;
	bottom: 0;
	left: 0;
	height: 50%;
	width: 50%;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
	table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-content:after,
.site-footer:after {
	clear: both;
}

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
body,
button,
input,
select,
textarea {
	color: #4d5256;
	font-family: "Spoqa Han Sans", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	font-size: 16px;
	font-size: 1rem;
	line-height: 1.6;
}

@media all and (min-width: 700px) {
	body,
	button,
	input,
	select,
	textarea {
		font-size: 20px;
		font-size: 1.25rem;
	}
}

h1, h2, h3, h4, h5, h6 {
	font-family: "Oswald", "Spoqa Han Sans", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	color: #363a3c;
	font-weight: 400;
	clear: both;
	margin: 1em 0 0.5em;
}

h1 strong, h2 strong, h3 strong, h4 strong, h5 strong, h6 strong {
	font-weight: 400;
}

h1 {
	font-family: "Oswald";
	font-size: 3em;
}

h2 {
	font-family: "Oswald";
	font-size: 2em;
}

h3 {
	font-size: 1.8em;
}

h4 {
	font-size: 1.6em;
}

h5 {
	font-size: 1.6em;
	font-family: "Satisfy", cursive;
}

h6 {
	font-size: 1em;
	color: #878d91;
	text-transform: uppercase;
	letter-spacing: 2px;
}

p {
	margin-bottom: 1.5em;
}

dfn, cite, em, i {
	font-style: italic;
	font-weight: 300;
}

cite, q {
	font-family: "Satisfy", cursive;
	font-style: normal;
}

strong, dt {
	color: #363a3c;
}

blockquote {
	margin: 1em 0em;
	font-weight: 100;
	font-size: 24px;
	font-size: 1.5rem;
	background: #f8fafb;
	padding: 2.5em 1.5em 1.5em;
	-webkit-transform: skewX(14deg);
	transform: skewX(14deg);
	font-style: italic;
}

blockquote:before, blockquote:after {
	content: "";
	position: absolute;
	width: 160px;
	border-top: 2px solid gainsboro;
}

blockquote:before {
	margin-top: -1em;
}

blockquote p {
	margin-bottom: 1em;
	quotes: "“" "”" "‘" "’";
}

blockquote p:before {
	content: open-quote;
}

blockquote p:after {
	content: close-quote;
}

blockquote dfn, blockquote em, blockquote i {
	font-style: normal;
}

blockquote cite, blockquote q {
	font-style: italic;
}

address {
	margin: 0 0 1.5em;
	font-weight: 300;
}

pre {
	background: #363a3c;
	font-family: "Noto Mono", Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 15px;
	font-size: 0.9375rem;
	color: #a9afb3;
	line-height: 1.5;
	margin-bottom: 1.6em;
	max-width: 100%;
	overflow: auto;
	padding: 1.6em;
}

code, kbd, tt, var {
	font-family: "Noto Mono", Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
	font-size: 18.75px;
	font-size: 1.17188rem;
	background: #f8fafb;
	padding: 0.3em 0.5em;
}

var {
	font-weight: 300;
}

abbr, acronym {
	color: #878d91;
	border-bottom: 1px dotted #878d91;
	text-decoration: none;
	cursor: help;
	font-style: italic;
	font-weight: 300;
}

mark, ins {
	background: #fff9c0;
	text-decoration: none;
}

big {
	font-size: 26px;
	font-size: 1.625rem;
}

blockquote, q {
	quotes: "" "";
}

blockquote:before, blockquote:after, q:before, q:after {
	content: "";
}

.cursive {
	font-family: "Satisfy", cursive;
	font-size: 26px;
	font-size: 1.625rem;
}

/*--------------------------------------------------------------
# Forms
--------------------------------------------------------------*/
input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"],
textarea {
	color: #666;
	border: 1px solid #ccc;
	border-radius: 3px;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="url"]:focus,
input[type="password"]:focus,
input[type="search"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="range"]:focus,
input[type="date"]:focus,
input[type="month"]:focus,
input[type="week"]:focus,
input[type="time"]:focus,
input[type="datetime"]:focus,
input[type="datetime-local"]:focus,
input[type="color"]:focus,
textarea:focus {
	color: #111;
}

select {
	border: 1px solid #ccc;
}

input[type="text"],
input[type="email"],
input[type="url"],
input[type="password"],
input[type="search"],
input[type="number"],
input[type="tel"],
input[type="range"],
input[type="date"],
input[type="month"],
input[type="week"],
input[type="time"],
input[type="datetime"],
input[type="datetime-local"],
input[type="color"] {
	padding: 3px;
}

textarea {
	padding-left: 3px;
	width: 100%;
}

/*--------------------------------------------------------------
# Buttons
--------------------------------------------------------------*/
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
	border: 1px solid;
	border-color: #ccc #ccc #bbb;
	background: transparent;
	font-size: 15px;
	font-size: 0.9375rem;
	line-height: 1;
	padding: .6em 1em .4em;
}

button:hover, button:active, button:focus,
input[type="button"]:hover,
input[type="button"]:active,
input[type="button"]:focus,
input[type="reset"]:hover,
input[type="reset"]:active,
input[type="reset"]:focus,
input[type="submit"]:hover,
input[type="submit"]:active,
input[type="submit"]:focus {
	border-color: #aaa #bbb #bbb;
	box-shadow: inset 0 -1px 0 rgba(255, 255, 255, 0.5), inset 0 2px 5px rgba(0, 0, 0, 0.15);
}

/*--------------------------------------------------------------
# Formattings
--------------------------------------------------------------*/
hr {
	background-color: #ccc;
	border: 0;
	height: 1px;
	margin-bottom: 1.5em;
}

/*--------------------------------------------------------------
# Lists
--------------------------------------------------------------*/
ul, ol {
	margin: 0 0 1.5em 3em;
}

ul {
	list-style: disc;
}

ol {
	list-style: decimal;
}

li > ul,
li > ol {
	margin-bottom: 0;
	margin-left: 1.5em;
}

dt {
	font-weight: bold;
}

dd {
	margin: 0 1.5em 1.5em;
}

/*--------------------------------------------------------------
# Tables
--------------------------------------------------------------*/
table {
	margin: 0 auto 1.5em;
	background: #363a3c;
	color: #ffffff;
	font-size: 18px;
	font-size: 1.125rem;
	font-weight: 300;
	padding: 2em;
}

table th, table td {
	padding: 0.2em 1.2em;
}

/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
.comment-navigation,
.posts-navigation,
.post-navigation,
.paging-navigation {
	padding: 2em 0;
	font-family: "Spoqa Han Sans", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	text-align: left;
	overflow: hidden;
	max-width: 75rem;
	margin: 0 auto;
}

.comment-navigation a,
.posts-navigation a,
.post-navigation a,
.paging-navigation a {
	display: block;
	width: 100%;
	text-decoration: none;
	padding: 2em;
	border: 1px solid #cccccc;
	color: #363a3c;
}

.comment-navigation a:hover, .comment-navigation a:focus,
.posts-navigation a:hover,
.posts-navigation a:focus,
.post-navigation a:hover,
.post-navigation a:focus,
.paging-navigation a:hover,
.paging-navigation a:focus {
	box-shadow: 0px 0px 15px #878d91;
	border: 1px solid gainsboro;
	color: #ffffff;
	border-color: #51CF66;
	/* Vivid colors */
	background: #51CF66;
	/* Old browsers */
	background: -moz-linear-gradient(left, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* FF3.6-15 */
	background: -webkit-linear-gradient(left, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to right, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#66F485', endColorstr='#17ACFE',GradientType=1 );
	/* IE6-9 */
}

.nav-links {
	margin: 0 auto;
	padding: 0 .4em;
}

.nav-previous {
	padding-bottom: 1em;
}

@media screen and (min-width: 62.5rem) {
	.nav-links {
		display: flex;
		justify-content: space-between;
		align-items: stretch;
		padding: 0;
	}
	.comment-navigation .nav-previous,
	.comment-navigation .nav-next,
	.posts-navigation .nav-previous,
	.posts-navigation .nav-next,
	.post-navigation .nav-previous,
	.post-navigation .nav-next {
		float: left;
		display: flex;
		align-items: stretch;
		width: 48%;
	}
	.comment-navigation .nav-next,
	.posts-navigation .nav-next,
	.post-navigation .nav-next {
		float: right;
		text-align: right;
	}
	.nav-previous {
		padding-bottom: 0;
	}
}

.post-navigation {
	margin-bottom: 3em;
}

.post-navigation .meta-nav {
	display: block;
	font-size: 16px;
	font-size: 1rem;
	color: #878d91;
	font-weight: 300;
}

@media screen and (min-width: 30em) {
	.post-navigation {
		padding-bottom: 1em;
	}
}

.post-navigation .post-title {
	font-size: 28px;
	font-size: 1.75rem;
	font-weight: 100;
	color: #1d1f21;
}

.post-navigation a:hover,
.post-navigation a:focus {
	color: #ffffff !important;
}

.pagination {
	font-family: "Spoqa Han Sans", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
}

.pagination .nav-links {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}

.pagination a,
.pagination span {
	padding: .5em 1em;
	background: white;
}

.pagination a {
	text-decoration: none;
}

.pagination a:focus, .pagination a:hover {
	color: white;
	background: #51CF66;
}

.pagination .current {
	font-weight: 600;
	color: #51CF66;
}

.pagination .prev .icon {
	margin-right: 1em;
}

.pagination .next .icon {
	margin-left: 1em;
}

/*--------------------------------------------------------------
# Links
--------------------------------------------------------------*/
a {
	color: #51CF66;
}

a:visited {
	color: rgba(81, 207, 102, 0.8);
}

a:hover, a:focus, a:active {
	color: white;
	background-color: #51CF66;
}

a:focus {
	outline: thin dotted;
}

a:hover, a:active {
	outline: 0;
}

/*--------------------------------------------------------------
# Layout (Content)
--------------------------------------------------------------*/
html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	/* Inherit box-sizing to make it easier to change the property for components that leverage other behavior; see http://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/ */
	box-sizing: inherit;
}

body {
	background: #fff;
	/* Fallback for when there is no custom background color defined. */
}

.row {
	max-width: 75rem;
	margin-left: auto;
	margin-right: auto;
}

.row::before, .row::after {
	content: ' ';
	display: table;
}

.row::after {
	clear: both;
}

.row.collapse > .column, .row.collapse > .columns {
	padding-left: 0;
	padding-right: 0;
}

.row .row {
	max-width: none;
	margin-left: -0.625rem;
	margin-right: -0.625rem;
	margin-left: 0rem;
	margin-right: 0rem;
	max-width: none;
}

@media screen and (min-width: 40em) {
	.row .row {
		margin-left: -0.9375rem;
		margin-right: -0.9375rem;
		margin-left: 0rem;
		margin-right: 0rem;
	}
}

.row .row.collapse {
	margin-left: 0;
	margin-right: 0;
}

.row.expanded {
	max-width: none;
}

.row.expanded .row {
	margin-left: auto;
	margin-right: auto;
}

.site-content {
	padding: 1em;
}

@media screen and (min-width: 37.5rem) {
	.site-content {
		padding: 2em;
	}
}

/*--------------------------------------------------------------
## Single Column / No Active Sidebar
--------------------------------------------------------------*/
.no-sidebar .site-main {
	margin: 0;
}

.no-sidebar .content-area {
	float: none;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
## Posts
--------------------------------------------------------------*/
.sticky {
	display: block;
}

.hentry {
	margin: 0 0 1.5em;
}

.entry-title,
.page-title {
	font-family: "Oswald", "Satisfy", cursive;
	text-align: center;
	margin: 0.5em;
}

.entry-meta {
	display: block;
	text-align: center;
	margin: 0 0 4em 0em;
	color: #878d91;
	font-size: 15px;
	font-size: 0.9375rem;
	font-weight: 300;
}

.entry-meta a {
	color: #878d91;
	text-decoration: none;
	border-bottom: 1px dotted gainsboro;
}

.byline,
.updated:not(.published) {
	display: none;
}

.single .byline,
.group-blog .byline {
	display: inline;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.breadcrumbs {
	display: block;
	color: #878d91;
	font-size: 15px;
	font-size: 0.9375rem;
	font-weight: 300;
	margin-bottom: 2em;
	margin-left: -6px !important;
}

.breadcrumbs a {
	color: #878d91;
	text-decoration: none;
}

.breadcrumbs a {
	padding: 0 15px 5px 10px;
	margin-right: 5px;
}

.breadcrumbs a:before {
	background: #fff;
	content: "";
	height: 50%;
	left: 0;
	position: absolute;
	bottom: 0;
	-webkit-transform: skewX(14deg);
	transform: skewX(14deg);
	width: 100%;
	z-index: -1;
	border-right: 1px solid gainsboro;
	border-bottom: 1px solid gainsboro;
}

.entry-header-container {
	background: #f8fafb;
	margin: -2em -2em 2em;
	padding: 3.5em 2em 0.25em;
}

.entry-header-container .post-thumbnail-container {
	width: 402px;
	height: 402px;
	text-align: center;
	position: relative;
	margin: 0 auto;
	border: 1px solid transparent;
}

.entry-header-container .post-thumbnail-container:hover {
	box-shadow: 0px 0px 15px #878d91;
	border: 1px solid gainsboro;
	border-radius: 50%;
}

.entry-header-container .post-thumbnail {
	margin: 0 auto;
	width: 400px;
	height: 400px;
	background-size: cover !important;
	border-radius: 50%;
}

.entry-header,
.entry-content {
	max-width: 42em !important;
	margin: 0 auto;
}

.entry-content {
	font-size: 18px;
	font-size: 1.125rem;
}

.entry-content > p:first-child:first-letter {
	font-size: 3.5em;
	line-height: 0.6;
	float: left;
	float: left;
	padding-top: 0.2em;
	padding-right: 0.05em;
}

.entry-content > p:first-child:first-line {
	font-family: "Noto Serif", Georgia, Cambria, "Times New Roman", Times, serif;
	text-transform: uppercase;
	letter-spacing: 2px;
}

.entry-content:after {
	content: "끝";
	font-family: "Satisfy", cursive;
	content: "End";
	font-size: 80px;
	font-size: 5rem;
	text-align: center;
	display: block;
	margin: 1.5em auto;
}

.tags-links {
	max-width: 75rem;
	margin: 1em auto;
	display: block;
	color: #878d91;
	font-size: 15px;
	font-size: 0.9375rem;
	font-weight: 300;
}

.tags-links a {
	color: #878d91;
	text-decoration: none;
}

/*--------------------------------------------------------------
## Pages
--------------------------------------------------------------*/
.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

/*--------------------------------------------------------------
# Comments
--------------------------------------------------------------*/
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

#comments {
	background: #f8fafb;
	margin: 0 -2em;
	padding: 2em;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
	margin: 0 0 1.5em;
	/* Make sure select elements fit in widgets. */
}

.widget select {
	max-width: 100%;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
img {
	height: auto;
	/* Make sure images are scaled correctly. */
	max-width: 100%;
	/* Adhere to container width. */
}

.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

.wp-caption {
	margin: 1em 0 1.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text {
	padding: 0.8075em 0;
	background: #f8fafb;
	display: block;
	text-align: center;
	margin: 0;
	color: #878d91;
	font-size: 15px;
	font-size: 0.9375rem;
	font-weight: 300;
	-webkit-transform: skewX(14deg);
	transform: skewX(14deg);
	font-style: italic;
	margin-left: 6px;
	margin-right: -6px;
}

.wp-caption .wp-caption-text a {
	color: #878d91;
	text-decoration: none;
	border-bottom: 1px dotted gainsboro;
}

.wp-caption .wp-caption-text:after {
	content: "";
	position: absolute;
	bottom: 0;
	left: 0;
	width: 160px;
	max-width: 50%;
	border-bottom: 2px solid gainsboro;
}

/* Ensure center-aligned captions center-align properly. */
.wp-caption.aligncenter {
	margin-left: auto;
	margin-right: auto;
}

/* Give some padding to floated elements, to prevent them butting up against text. */
.wp-caption.alignleft {
	margin-right: 20px;
}

.wp-caption.alignright {
	margin-left: 20px;
}

.wp-caption-text {
	text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
}

/*--------------------------------------------------------------
# K2K Styles
--------------------------------------------------------------*/
/*--------------------------------------------------------------
# Header
--------------------------------------------------------------*/
.header-image {
	margin: 0;
	max-height: 60vh;
	background-size: cover;
	background-position-x: center;
}

.site-header {
	padding: 1em;
	font-family: "Spoqa Han Sans", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	background-color: #86AF49;
}

@media screen and (min-width: 37.5rem) {
	.site-header {
		padding: 2em 2em;
	}
}

.site-header.not-front {
	max-height: 140px;
}

.site-header.not-front .site-branding {
	display: block;
	margin: 0 auto;
	width: 100%;
	text-align: center;
}

.site-header.not-front .site-branding-text {
	display: none;
}

.gradient-overlay {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: #86af49;
	/* Old browsers */
	background: -moz-linear-gradient(left, rgba(134, 175, 73, 0.8) 0%, rgba(0, 98, 163, 0.8) 100%);
	/* FF3.6-15 */
	background: -webkit-linear-gradient(left, rgba(134, 175, 73, 0.8) 0%, rgba(0, 98, 163, 0.8) 100%);
	/* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to right, rgba(134, 175, 73, 0.8) 0%, rgba(0, 98, 163, 0.8) 100%);
	/* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#86af49', endColorstr='#0062a3',GradientType=1 );
	/* IE6-9 */
	/* Vivid colors */
	background: #51CF66;
	/* Old browsers */
	background: -moz-linear-gradient(left, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* FF3.6-15 */
	background: -webkit-linear-gradient(left, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* Chrome10-25,Safari5.1-6 */
	background: linear-gradient(to right, rgba(81, 207, 102, 0.8) 0%, rgba(50, 154, 240, 0.8) 100%);
	/* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#66F485', endColorstr='#17ACFE',GradientType=1 );
	/* IE6-9 */
}

.site-branding {
	/*display: flex;*/
	min-height: 65px;
}

.custom-logo-link {
	margin-right: 1em;
	flex: 1 0 auto;
}

.custom-logo-link img {
	display: inline;
	height: 65px;
	width: auto;
}

.site-branding-text {
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin: 1em 0;
}

.site-title {
	font-family: "Spoqa Han Sans", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Geneva, Arial, sans-serif;
	margin: 0;
	padding: 0;
	font-size: 32px;
	font-size: 2rem;
	font-weight: 700;
	line-height: 1em;
}

.site-title a {
	color: white;
	text-decoration: none;
}

.site-title a:hover, .site-title a:focus {
	text-decoration: underline;
}

.site-description {
	margin: 0;
	font-size: 18px;
	font-size: 1.125rem;
	font-weight: 300;
}

.site-description a {
	color: #fff;
}

@media screen and (min-width: 37.5rem) {
	.site-header {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	.site-branding {
		width: 35%;
		flex: 0 0 auto;
	}
	.main-navigation {
		width: 55%;
		flex: 0 0 auto;
	}
}

/*--------------------------------------------------------------
# Header Flash
--------------------------------------------------------------*/
.header-flash {
	max-width: 75rem;
	margin: 0 auto;
	background: #ffffff;
	font-size: 14px;
	font-size: 0.875rem;
	font-weight: 300;
	color: #878d91;
}

.header-flash ul {
	margin: 10px 0;
	list-style: none;
}

.header-flash ul li {
	display: inline;
}

.header-flash ul li:after {
	content: "";
	border-right: 1px solid #878d91;
	margin: 0 10px;
	height: 100%;
}

.header-flash ul li:last-child:after {
	border: none;
}

/*--------------------------------------------------------------
# Header Search
--------------------------------------------------------------*/
.search-toggle {
	position: absolute;
	top: 0;
	right: 0;
	background: rgba(255, 255, 255, 0.8);
	width: 2em;
	height: 2em;
	z-index: 1;
}

.search-box-wrapper {
	position: fixed;
	top: 0;
	height: 3em;
	background: white;
	z-index: 1;
	width: 100%;
	padding: 0.25em 1em;
}

.search-box-wrapper .search-field {
	width: calc(100% - 2em);
}

.search-box-wrapper .search-submit {
	display: none;
}

/*--------------------------------------------------------------
# Header Menu
--------------------------------------------------------------*/
.main-navigation {
	clear: both;
	display: block;
	font-size: 18px;
	font-size: 1.125rem;
}

.main-navigation ul {
	display: none;
	list-style: none;
	margin: 0;
	padding-left: 0;
}

.main-navigation ul ul {
	box-shadow: 0 3px 3px rgba(0, 0, 0, 0.2);
	float: left;
	position: absolute;
	top: 1.5em;
	left: -999em;
	z-index: 99999;
}

.main-navigation ul ul ul {
	left: -999em;
	top: 0;
}

.main-navigation ul ul li:hover > ul,
.main-navigation ul ul li.focus > ul {
	left: 100%;
}

.main-navigation ul ul a {
	width: 200px;
}

.main-navigation ul li:hover > ul,
.main-navigation ul li.focus > ul {
	left: auto;
}

.main-navigation li {
	/*float: left;*/
	text-align: right;
	border-bottom: 1px dotted #ffffff;
	position: relative;
	font-weight: 300;
	line-height: 2;
	/*padding-right: 0.25em;*/
}

.main-navigation a {
	display: block;
	text-decoration: none;
	color: #ffffff;
}

.main-navigation .dropdown-toggle {
	position: absolute;
	top: 0;
	right: -2em;
	padding: 0;
	border: none;
	color: #ffffff;
	width: 2em;
	height: auto;
	line-height: 2;
}

.not-front.main-navigation {
	width: 100%;
	border-bottom: 1px solid gainsboro;
	font-size: 0.8em;
	height: 76px;
}

.not-front.main-navigation .menu-main-menu-container {
	max-width: 75rem;
	margin: 0 auto;
}

.not-front.main-navigation ul {
	list-style: none;
	float: right;
	position: relative;
}

.not-front.main-navigation ul ul {
	display: none;
}

.not-front.main-navigation li {
	display: inline-block;
	padding: 20px 10px;
}

.not-front.main-navigation a {
	color: #878d91;
}

/* Small menu. */
.menu-toggle,
.main-navigation.toggled ul {
	display: block;
}

.menu-toggle:before {
	content: "\2630";
	display: inline-block;
	margin-right: 5px;
}

@media screen and (min-width: 37.5rem) {
	.menu-toggle {
		display: none;
	}
	.main-navigation ul {
		display: block;
	}
}

/*--------------------------------------------------------------
# Primary (Main Content)
--------------------------------------------------------------*/
.site-search-overlay {
	transition: all 0.5s;
	width: 0%;
	height: 6em;
	top: calc( 50% - 2em);
	left: 0;
	z-index: 9;
	background: rgba(9, 74, 128, 0.8);
	position: fixed;
	padding-top: 20px;
}

.site-search-overlay .search-form {
	padding: 1em 0;
}

.site-search-overlay .search-form .search-field {
	transition: all 0.5s;
	font-size: 40px;
	font-size: 2.5rem;
	font-weight: 300;
	font-family: "Oswald";
	text-align: center;
	background: rgba(9, 74, 128, 0.8);
	color: #ffffff;
	border: none;
	width: 0%;
	margin-top: -15px;
	margin-left: -10px;
}

.site-search-overlay .search-form .search-field:focus, .site-search-overlay .search-form .search-field:active {
	outline: none;
}

.site-search-overlay .search-form .search-submit {
	display: none;
}

.site-search-overlay.active {
	width: 100%;
	padding-left: 60px;
}

.site-search-overlay.active .search-field {
	width: calc(100vw - 125px);
	border: 1px solid;
}

/*--------------------------------------------------------------
# Secondary (Sidebar)
--------------------------------------------------------------*/
aside#secondary {
	font-size: 16px;
	font-size: 1rem;
	transition: all 0.5s;
	padding: 3em;
	position: fixed;
	width: 37.5rem;
	max-width: 37.5rem;
	top: 0;
	right: -37.5rem;
	color: white;
	background: rgba(9, 74, 128, 0.8);
	overflow-y: scroll;
	/*    &:before {
        font-family: "FontAwesome";
        content: "\f04a";
        display: block;
        position: fixed;
        top: 50%;
        right: 0;
        height: 40px;
        width: 40px;
        //background: $color__dark_green_alpha;
        background: darken( $color__bright_blue_alpha, 30% );
        padding: 8px;
        text-align: center;
    }*/
}

aside#secondary:hover, aside#secondary:active, aside#secondary:focus, aside#secondary.active {
	right: 0%;
	height: 100%;
}

aside#secondary:hover:before, aside#secondary:active:before, aside#secondary:focus:before, aside#secondary.active:before {
	display: none;
}

.site-side-button {
	color: white;
	display: block;
	position: fixed;
	top: 50%;
	right: 0;
	height: 40px;
	width: 40px;
	background: rgba(9, 74, 128, 0.8);
	padding: 3px;
	text-align: center;
	z-index: 10;
}

.site-search-button {
	left: 0;
}

/*--------------------------------------------------------------
# WPAdminBar Fix
--------------------------------------------------------------*/
@media screen and (max-width: 600px) {
	#wpadminbar {
		top: -46px;
	}
}
